@import "./src/assets/scss/_mixins.scss";
              @import "./src/assets/scss/_variables.scss";

.popup {
  @include gridable();
  position: relative;
  justify-content: stretch;
  justify-items: center;
  box-sizing: border-box;
  padding: 75px 0 120px;
  background-color: $color-background;
  gap: 120px;

  @media screen and (max-width: $tablet-s) {
    gap: 45px;
  }

  &__button-close {
    @include cross(70px, $color-text, 6px);
    position: absolute;
    top: 60px;
    right: 50px;

    @media screen and (max-width: $desktop-l) {
      @include cross(45px, $color-text, 4px);
      position: absolute;
      top: 33px;
      right: 33px;
    }

    @media screen and (max-width: $tablet-s) {
      @include cross(25px, $color-text, 2px);
      position: absolute;
      top: 16px;
      right: 16px;
    }

    &::after,
    &::before {
      transition: background-color 0.5s ease;
    }

    &:hover::after,
    &:hover::before {
      background-color: $color-decorate;
    }
  }
}
